import React, { useContext, useEffect, useState } from "react";
import { ShopContext } from "../context";

function Categories() {
  const { filterGoods } = useContext(ShopContext);
  const [category, setCategory] = useState("");
  useEffect(() => {
    filterGoods(category);
  }, [category]);
  return (
    <div className="categories-mobile  categories">
      <form
        action="#"
        className="categories-form scale-up-center "
        onClick={(e) => e.stopPropagation()}
      >
        <p>
          <label className="black-text">
            <input
              name="group1"
              type="radio"
              value=""
              onChange={(e) => setCategory(e.target.value)}
              checked={category === ""}
            />
            <span></span>
          </label>
        </p>
        <p>
          <label className="black-text">
            <input
              name="group1"
              type="radio"
              value=""
              onChange={(e) => setCategory(e.target.value)}
              checked={category === ""}
            />
            <span></span>
          </label>
        </p>
        <p>
          <label className="black-text">
            <input
              name="group1"
              type="radio"
              value=""
              onChange={(e) => setCategory(e.target.value)}
              checked={category === ""}
            />
            <span></span>
          </label>
        </p>
        <p>
          <label className="black-text">
            <input
              name="group1"
              type="radio"
              value=""
              onChange={(e) => setCategory(e.target.value)}
              checked={category === ""}
            />
            <span></span>
          </label>
        </p>
        <p>
          <label className="black-text">
            <input
              name="group1"
              type="radio"
              value=""
              onChange={(e) => setCategory(e.target.value)}
              checked={category === ""}
            />
            <span> </span>
          </label>
        </p>
        <p>
          <label className="black-text">
            <input
              name="group1"
              type="radio"
              value=""
              onChange={(e) => setCategory(e.target.value)}
              checked={category === ""}
            />
            <span></span>
          </label>
        </p>
      </form>
    </div>
  );
}

export default Categories;
